// <Desktop-specific layout - secondary navigation never present
main > .school {
    @include media('<small') {
        //@include default-margin();
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > .school {
            grid-column: 6 / span 14;
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .school {
                grid-column: 7 / span 18;
                @include content-grid();

                .school__top-gradient {
                    grid-column: 1 / span 18;
                }

                .school__content {
                    grid-column: 1 / -1;

                    .school__content__cta,
                    .school__content__links,
                    .school__content__facilities,
                    .promo-area__wrap,
                    .school__content__pathway,
                    .school__content__video,
                    .school__content__news,
                    .school__content__events,
                    .school__content__middle-wrapper {
                        grid-column: 1 / span18;
                    }

                    .school__statistic {
                        @include content-grid();

                        .statistics-block,
                        h2 {
                            grid-column: 3/17;
                        }
                    }

                    .school__content__links {
                        @include content-grid();

                        .school__content__links--left {
                            grid-column: 1/10;
                        }

                        .school__content__links--right {
                            grid-column: 10/19;
                        }
                    }
                }
            }
        }
    }
}

@include media('>mobile') {
    .school__content__news,
    .school__content__events {
        .related-content {
            display: grid;
            @include x-column-grid(2);
            column-gap: $measure--basic;
        }
    }
}

@include media('>=small') {
    .school__content__pathway {
        .pathway {
            margin-top: 0;
            padding-top: 0;
        }
        .pathway-image-cards__blocks {
            display: grid;
            @include x-column-grid(2);
        }
    }

    .school__content__news {
        .related-content {
            display: grid;
            @include x-column-grid(3);
        }
    }

    .school__content__events {
        .related-content {
            display: grid;
            @include x-column-grid(3);
        }
    }

    .school__content__pathway {
        .pathway__cards {
            .card--pathway {
                width: calc(50% - 1rem);
            }
        }
    }
}

@include media('<small') {
    .school__content__news,
    .school__content__events,
    .school__content__cta,
    .school__content__links,
    .promo-area__wrap,
    .school__content__facilities {
        @include default-margin();
    }

    .school__content__links--left {
        margin-bottom: $measure--basic;
    }
}

.school__top-gradient {
    background-image: linear-gradient(to top, $color-purewhite, $color-gray1);

    .cta-block {
        max-width: initial;
    }
}

.school__content__middle-wrapper {
    background-color: $color-gray1;
}

.school__content__links {
    .school__content__links--left {
        ul {
            list-style: none;
            margin-left: 0;
        }
    }

    .school__content__links--right {
        background-color: $color-blue70;
        padding: $measure--basic;

        h2 {
            color: $color-purewhite;
            margin-top: 0;
        }

        ul {
            list-style: none;
            margin-left: 0;
            margin-top: 0;
            max-width: unset;

            @include media('<small') {
                margin-bottom: $measure--basic;
            }

            @include media('>=tablet') {
                columns: 2;
            }

            li:first-of-type {
                margin-top: 0;
            }
        }

        .school__anchor-inline--gray4--darkColor {
            @include anchor-inline(
                $color-gray4,
                $color-gray4,
                $color-nearblack,
                $color-outline--dark
            );
        }
    }
}

.school__content__cta {
    @include media('>tablet') {
        margin-bottom: $measure--small4;

        ul {
            align-items: center;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: center;

            li {
                max-width: calc(33% - #{$measure--basic});
                margin-right: $measure--basic;
            }
        }
    }

    ul {
        list-style: none;
        margin-left: 0;
        margin-top: 0;
        max-width: 100%;

        li {
            margin-top: 0;
            text-align: center;

            @include media('<tablet') {
                p {
                    max-width: initial;
                }
            }
        }
    }

    .school__cta-outlined-light-bg {
        //@include cta-outlined-light-bg($color-gray11, $color-gray6);
        @include cta-outlined-dark-bg(
            $color-gray6,
            $color-gray11,
            $color-nearblack
        );

        @include media('<tablet') {
            display: flex;
            justify-content: center;
            text-align: center;
        }
    }
}

.school__statistic {
    position: relative;
    h2 {
        text-align: center;
        position: relative;
        padding-bottom: $measure--half;

        &:after {
            content: '';
            display: block;
            position: absolute;
            width: 10%;
            left: 45%;
            border-bottom: $border-width-thicker solid $color-red50;
            bottom: -$measure--half;
        }
    }

    .statistics-block {
        .ksp__items {
            margin-left: 0;
            margin-right: 0;
        }
    }

    .ksp__items {
        background-color: $color-purewhite;
    }

    &:after {
        content: '';
        position: absolute;
        background-color: $color-gray1;
        height: 100%;
        width: 100%;
        top: $measure--medium5;
        z-index: z-index('below');
    }

    .ksp__items {
        padding-top: $measure--medium3;
        padding-bottom: $measure--small4;
    }
}

.school__content__links {
    margin-top: $measure--small4;
    margin-bottom: $measure--small4;

    .related-links--with-images__links-block {
        .icon {
            color: $color-red50;
        }
    }
}

//School of Health Sciences
.shs {
    .banner__standfirst {
        h1 {
            color: $color-blue80;
        }
    }
    .banner__standfirst-cta {
        @include cta-basic-light-bg($color-blue80, $color-purewhite);
    }
    .school__content__links--right {
        background-color: $color-blue80;
    }

    .school__content__links--left {
        &:hover {
            .linked-heading__text {
                &:before {
                    background: $color-blue60;
                }
            }
        }

        .linked-heading__icons {
            background: $color-blue80;
        }
    }

    .school__statistic {
        h2 {
            &:after {
                border-bottom-color: $color-blue80;
            }
        }
    }
}

