@mixin slider-properties() {
    overflow: hidden;
    padding: $measure--basic;
    position: relative;

    .slide {
        top: 0;
        transition: left $transition-duration--medium ease-in-out;
        width: 100%;

        @include media('reducemotion') {
            transition: none;
        }
    }
}

.js {
    .slider {
        &[data-small='true'] {
            @include media('<tablet') {
                @include slider-properties();
            }

            @include media('>=tablet') {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
            }
        }

        &:not([data-small='true']) {
            @include media('<tablet') {
                @include slider-properties();
            }

            @include media('>=tablet') {
                @include slider-properties();

                &[data-style='buttons'] {
                    display: flex;
                    flex-direction: row;
                    flex-wrap: nowrap;
                }
            }
        }

        .slide {
            width: 100%;

            &:focus {
                @include outline-properties($color-nearblack);

                outline-offset: -#{$border-width-thick};
            }
        }

        &[data-rowsize='1'] {
            &[data-small='true'] {
                @include media('<tablet') {
                    .slide {
                        &[data-smallposition='-1'] {
                            left: -100%;
                            position: absolute;

                            &[data-smallhidden='true'] {
                                visibility: hidden;
                            }
                        }

                        &[data-smallposition='0'] {
                            left: 0;
                            position: relative;
                        }

                        &[data-smallposition='1'] {
                            left: 100%;
                            position: absolute;

                            &[data-smallhidden='true'] {
                                visibility: hidden;
                            }
                        }
                    }
                }

                @include media('>=tablet') {
                }
            }

            &:not([data-small='true']) {
                .slide {
                    &[data-smallposition='-1'] {
                        left: -100%;
                        position: absolute;

                        &[data-smallhidden='true'] {
                            visibility: hidden;
                        }
                    }

                    &[data-smallposition='0'] {
                        left: 0;
                        position: relative;
                    }

                    &[data-smallposition='1'] {
                        left: 100%;
                        position: absolute;

                        &[data-smallhidden='true'] {
                            visibility: hidden;
                        }
                    }
                }
            }
        }

        &[data-rowsize='2'] {
            &[data-small='true'] {
                @include media('<tablet') {
                    .slide {
                        &[data-smallposition='-1'] {
                            left: -100%;
                            position: absolute;

                            &[data-smallhidden='true'] {
                                visibility: hidden;
                            }
                        }

                        &[data-smallposition='0'] {
                            left: 0;
                            position: relative;
                        }

                        &[data-smallposition='1'] {
                            left: 100%;
                            position: absolute;

                            &[data-smallhidden='true'] {
                                visibility: hidden;
                            }
                        }
                    }
                }

                @include media('>=tablet') {
                    margin-right: $measure--small2;
                    width: calc(50% - #{$measure--small2});
                }
            }

            &:not([data-small='true']) {
                @include media('<tablet') {
                    .slide {
                        &[data-smallposition='-1'] {
                            left: -100%;
                            position: absolute;

                            &[data-smallhidden='true'] {
                                visibility: hidden;
                            }
                        }

                        &[data-smallposition='0'] {
                            left: 0;
                            position: relative;
                        }

                        &[data-smallposition='1'] {
                            left: 100%;
                            position: absolute;

                            &[data-smallhidden='true'] {
                                visibility: hidden;
                            }
                        }
                    }
                }

                @include media('>=tablet') {
                    .slide {
                        margin-right: $measure--small2;
                        width: calc(50% - #{$measure--small2});

                        &[data-sliderposition='-2'],
                        &[data-sliderposition='-1'] {
                            left: -100%;
                            position: absolute;

                            &[data-hidden='true'] {
                                visibility: hidden;
                            }
                        }

                        &[data-sliderposition='0'],
                        &[data-sliderposition='1'] {
                            left: 0;
                            position: relative;
                        }

                        &[data-sliderposition='2'],
                        &[data-sliderposition='3'] {
                            left: 100%;
                            position: absolute;

                            &[data-hidden='true'] {
                                visibility: hidden;
                            }
                        }
                    }
                }
            }
        }

        &[data-rowsize='3'] {
            &[data-small='true'] {
                @include media('<tablet') {
                    .slide {
                        &[data-smallposition='-1'] {
                            left: -100%;
                            position: absolute;

                            &[data-smallhidden='true'] {
                                visibility: hidden;
                            }
                        }

                        &[data-smallposition='0'] {
                            left: 0;
                            position: relative;
                        }

                        &[data-smallposition='1'] {
                            left: 100%;
                            position: absolute;

                            &[data-smallhidden='true'] {
                                visibility: hidden;
                            }
                        }
                    }
                }

                @include media('>=tablet') {
                    .slide {
                        margin-right: $measure--small2;
                        width: calc(33% - #{$measure--small2});
                    }
                }
            }

            &:not([data-small='true']) {
                @include media('<tablet') {
                    .slide {
                        &[data-smallposition='-1'] {
                            left: -100%;
                            position: absolute;

                            &[data-smallhidden='true'] {
                                visibility: hidden;
                            }
                        }

                        &[data-smallposition='0'] {
                            left: 0;
                            position: relative;
                        }

                        &[data-smallposition='1'] {
                            left: 100%;
                            position: absolute;

                            &[data-smallhidden='true'] {
                                visibility: hidden;
                            }
                        }
                    }
                }

                @include media('>=tablet') {
                    .slide {
                        margin-right: $measure--small2;
                        width: calc(33% - #{$measure--small2});

                        &[data-sliderposition='-3'],
                        &[data-sliderposition='-2'],
                        &[data-sliderposition='-1'] {
                            left: -100%;
                            position: absolute;

                            &[data-hidden='true'] {
                                visibility: hidden;
                            }
                        }

                        &[data-sliderposition='0'],
                        &[data-sliderposition='1'],
                        &[data-sliderposition='2'] {
                            left: 0;
                            position: relative;
                        }

                        &[data-sliderposition='3'],
                        &[data-sliderposition='4'],
                        &[data-sliderposition='5'] {
                            left: 100%;
                            position: absolute;

                            &[data-hidden='true'] {
                                visibility: hidden;
                            }
                        }
                    }
                }
            }
        }
    }

    .slider[data-style='numbers'] {
        & + .slider__controls {
            align-items: center;
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: center;
            margin-top: $measure--basic;

            .slider__indicator {
                @include smalltype();

                .slider__indicator__current,
                .slider__indicator__divider {
                    color: $color-gray7;
                }

                .slider__indicator__divider {
                    margin-left: $measure--quarter;
                    margin-right: $measure--quarter;

                    .slider__indicator__divider--sr {
                        @include sr-only();
                    }
                }

                .slider__indicator__total {
                    color: $color-red50;
                }
            }

            .slider__controls__prev,
            .slider__controls__next {
                background-color: transparent;
                border-color: $color-red50;
                border-style: $border-style-solid;
                border-width: $border-width-hairline;
                color: $color-red50;
                padding: $measure--threequarters;
                transition: background-color $transition-duration ease-in,
                    border-color $transition-duration ease-in,
                    color $transition-duration ease-in;

                @include media('reducemotion') {
                    /* We can't completely disable the transition becasuse we action on transitionend */
                    transition: background-color $transition-duration--instant
                            ease-in,
                        border-color $transition-duration--instant ease-in,
                        color $transition-duration--instant ease-in;
                }

                &:focus:not(:disabled),
                &:active:not(:disabled) {
                    @include outline-properties($color-nearblack);
                }

                &:hover:not(:disabled) {
                    @include media('hover') {
                        background-color: $color-red50;
                        color: $color-purewhite;
                    }
                }

                &:disabled {
                    border-color: transparent;
                    color: $color-gray7;
                    cursor: default;
                }
            }

            .slider__controls__prev,
            .slider__controls__next {
                @include font-family('icon');

                font-weight: $font-weight-normal;

                span {
                    @include sr-only();
                }
            }

            .slider__controls__prev {
                margin-left: $measure--basic;

                &:before {
                    content: '\f060';
                }

                & + .slider__controls__next {
                    margin-left: $measure--basic;
                }
            }

            .slider__controls__next {
                &:before {
                    content: '\f061';
                }
            }
        }

        &[data-small='true'] {
            & + .slider__controls {
                @include media('>=tablet') {
                    display: none;
                }
            }
        }
    }
}
